<template>
    <div class="main-page">
        <div class="headerTag">用车单-新增</div>
        <form class="h-form">
            <item must name="车辆">
                <input v-model="info.FCarNo" readonly @click="chooseCar()" />
            </item>
            <item must name="司机">
                <input v-model="info.DriverName" readonly @click="chooseDriver()" />
            </item>
            <item name="用车日期">
                <input v-model="info.FDate" readonly @click="chooseTime('FDate')" />
            </item>
            <item name="里程">
                <input v-model="info.Mileage" />
            </item>
            <!-- <item name="开始地点">
                <input v-model="info.FStartSite" />
            </item>
            <item name="结束地点">
                <input v-model="info.FEndSite" />
            </item> -->
            <item name="调出仓库">
                <input v-model="info.OutWarehouseName" readonly @click="chooseWarehouse(1)" />
            </item>
            <item name="调入仓库">
                <input v-model="info.InWarehouseName" readonly @click="chooseWarehouse(3)" />
            </item>
            <item name="选择笼车">
                <input v-model="info.CageName" readonly @click="chooseMaterial()" />
            </item>
            <item name="笼车数量">
                <input v-model="info.CageQty" />
            </item>
            <item name="经办人">
                <input v-model="info.OperatorName" readonly @click="choosePerson" />
            </item>
            <item name="用车说明">
                <textarea v-model="info.FNotes" />
            </item>
        </form>
        <van-button type="primary" block size="normal" :color="$config.theme" style="width: calc(100% - 30px); margin: 3px 15px" @click="submit" :loading="loading">提交</van-button>
        <van-calendar v-model="timeShow" @confirm="confirmTime" color="#409eff" />
    </div>
</template>

<script>
export default {
    name: "CarsUsingAE2",
    props: {},
    components: {},
    data() {
        return {
            // apis: {
            //     edit: this._api.VMS_CarUsingRecordEdit,
            //     create: this._api.VMS_CarUsingRecordCreate,
            //     list: this._api.VMS_CarUsingRecordManage,
            //     delete: this._api.VMS_CarUsingRecordDelete,
            //     audit: this._api.VMS_CarUsingRecordAudit,
            //     details: this._api.VMS_CarUsingRecordDetails,
            // },
            rules: {
                FCarNo: "请选择车辆！",
                DriverName: "请选择司机！",
                // FDate: "请选择用车日期！",
                // Mileage: "请输入里程！",
                // FStartSite: "请输入开始地点！",
                // FEndSite: "请输入结束地点！",
                // OutWarehouseName: "请选择调出仓库！",
                // InWarehouseName: "请选择调入仓库！",
                // CageName: "请选择笼车！",
                // CageQty: "请输入笼车数量！"
            },
            timeShow: false,
            loading: false,
            curTimeFieldName: "",
            info: {
                FType: 3
            },
            type: 1
        };
    },
    methods: {
        confirmTime(time) {
            this.timeShow = false;
            this.info[this.curTimeFieldName] =
                this.$time(time).format("YYYY-MM-DD");
        },
        chooseTime(fieldName, row) {
            this.curTimeFieldName = fieldName;
            this.timeShow = true;
        },
        async submit() {
            await this.$u.checkForm(this.info, this.rules);
            this.loading = true;
            this._api.VMS_CarUsingRecordCreate(this.info).then((res) => {
                if (res) {
                    this.$u.msg("操作成功！");
                    setTimeout(() => {
                        this.$router.back();
                    }, 200);
                }
                this.loading = false;
            });
        },
        chooseDriver() {
            window.temFun = (item) => {
                console.log(item)
                this.$set(this.info, "DriverId", item.Id);
                this.$set(this.info, "DriverName", item.FName);
            };
            this.$router.push(`/modular/oa/ChooseDriver`);
        },
        // 选择车辆
        chooseCar() {
            window.temFun = (item) => {
                this.$set(this.info, "CarId", item.Id);
                this.$set(this.info, "FCarNo", item.FCarNo);
                this.$set(this.info, "DriverId", item.DriverId);
                this.$set(this.info, "DriverName", item.DriverName);
            };
            this.$router.push(`/modular/oa/CarsChoose`);
        },
        choosePerson() {
            window.temFun = (person) => {
                this.$set(this.info, "OperatorName", person.Fname)
                this.$set(this.info, "OperatorId", person.Id)
            }
            this.$router.push(`/modular/oa/ChoosePerson`)
        },
        chooseWarehouse(type) {
            this.type = type
            window.temFun = (warehouse) => {
                if (this.type == 1) {
                    this.$set(this.info, "OutWarehouseName", warehouse.WarehouseName)
                    this.$set(this.info, "OutWarehouseId", warehouse.Id)
                } else if (this.type == 3) {
                    this.$set(this.info, "InWarehouseName", warehouse.WarehouseName)
                    this.$set(this.info, "InWarehouseId", warehouse.Id)
                }
            }
            this.$router.push(`/modular/oa/WarehouseChoose`)
        },
        chooseMaterial() {
            window.temFun = (item) => {
                this.$set(this.info, "CageId", item.Id);
                this.$set(this.info, "CageName", item.FName);
            }
            this.$router.push(`/modular/oa/MaterialChoose`)
        }
    },
    mounted() {
        this.init()
        this.info.FDate = this.$time(new Date()).format("YYYY-MM-DD")
        let myInfo = JSON.parse(localStorage.getItem("myInfo"))
        this.$set(this.info, "OperatorName", myInfo.Fname)
        this.$set(this.info, "OperatorId", myInfo.Id)
    },
};
</script>

<style lang="scss" scoped>
.h-title {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    box-shadow: 0 2px 0 0 rgba($color: #000000, $alpha: 0.1);
}

.car-item {
    position: relative;
    width: calc(100% - 30px);
    margin-left: 15px;
    margin-top: 15px;
    font-size: 14px;

    .car-item-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 40px;
        border-bottom: 1px solid #f2f2f2;
        color: #409eff;
        font-weight: 600;
        padding: 0 15px;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        overflow: hidden;

        .car-item-top-l {
            display: flex;
            align-items: center;

            .line {
                width: 3px;
                height: 16px;
                background: #409eff;
                border-radius: 3px;
                margin-right: 8px;
            }
        }

        &.active {
            color: #fff;
            background: #409eff;

            .line {
                background: #fff;
            }
        }
    }

    .car-item-bottom {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        height: 80px;
        padding: 0 15px;

        &.active {
            border: 1px solid #409eff;
            border-top: none;
        }

        .car-item-line {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 50%;
            white-space: nowrap;
            line-height: 40px;
            padding-right: 10px;

            input {
                height: 26px;
                width: 100px;
                text-align: left;
                border: 1px solid #eeeeee;
                padding: 0 3px;
            }
        }
    }
}
</style>